import React from 'react';
import s from "./Settings.module.css";

//     ()            .           .      -  ,   .       .
//      ,        !  ;)
//  ,      ,     .    , (..   )        .   -        .

// const BACK = 'BACK';

const Settings = (props) => {

    return <div className={s.osn}>
        {props.pages.map(p =>
            <div key={p.id} className={s.dis} onClick={() => { props.onClickPage(p.id) }}>
                <h2> {p.id} </h2>
            </div>)}
        {/* <button className={s.butt} onClick={() => {props.onClickPage(BACK)}}> </button> */}
        <button className={s.butt} onClick={() => { window.location.reload() }}> </button>
    </div>
}

export default Settings;